 import * as THREE from '../../node_modules/three/build/three.module';
 import {SVGLoader} from "../../node_modules/three/examples/jsm/loaders/SVGLoader"
 import {SVGRenderer,SVGObject} from "../../node_modules/three/examples/jsm/renderers/SVGRenderer"
 import bus from '../components/bus'



let timeout=null;
let camera;

let scene, renderer, svgLoader;
let character,group,groupB;
let tan, ele;
let pointNum=0,originVer=[],oDestiVer=[],destiVer=[],oVer=[];
let  originParticleField;
let len;
let currentIndex=0;
let speedX=4,speedY=4,speedZ=4;
let animationID=null;
let animationID1=null;
let stop=0;
let stop2=0;
let stop3=0;
let stop4=0;
let url,urls;
export function init(url_,urlb){
    url=url_;
    urls=urlb;
    len=urlb.length;
    let container = document.getElementById('ani');
    svgLoader = new SVGLoader();
    camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 10000);
    camera.position.z = 300;
    camera.position.y= 0;
    scene = new THREE.Scene();
    group=new THREE.Group();scene.add(group);
    character=new THREE.Group();
    character.name="character";
    groupB = new THREE.Group();
    groupB.name="groupB";
    group.add(groupB);

    group.add(character);
    
 
    camera.lookAt(scene.position);

    //人物加载
    for(let i=0;i<url.length;i++){
        svgLoader.load(url[i],
        svg_loading_done_callback1,
        function (xhr) { console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded') },
        function () {console.log("Error loading SVG!"); });
    }

    //背景加载
    for(let i=0;i<len;i++){
        svgLoader.load(urls[i],
            svg_loading_done_callback,
            function () { console.log("Loading SVG...");  },
            function () {console.log("Error loading SVG!"); });
    }

    group.rotation.x=Math.PI;
         group.translateY(-50);
    group.translateZ(100);
    character.translateY(100);


    renderer = new SVGRenderer();
    renderer.setClearColor(0x000000);
    renderer.setSize(container.clientWidth, container.clientHeight);
    container.appendChild(renderer.domElement);
    document.addEventListener("mousedown",onMouseDown,false);
    document.addEventListener("mouseup",onMouseUp,false);
    document.addEventListener("touchstart",onMouseDown,false);
    document.addEventListener("touchend",onMouseUp,false);
    window.addEventListener('resize', onWindowResize, false);

}


function svg_loading_done_callback1(doc){//人物回调

    init1(doc);      
    if(character.children.length==url.length){
        if(character.children[0].children.length==11){
            tan=0;ele=1;
        }else{
            tan=1;ele=0;
        }
        character.children[tan].name="tan";
        character.children[ele].name="elephant";
        groupRotate()

        //拷贝原数组；
        let obj={x:0,y:125,z:0};
        for(let i=0;i<pointNum;i++){
            oDestiVer.push(obj);
            oVer.push(obj);
        }             
        character.children[ele].visible=false;
        render();
    }
}

function svg_loading_done_callback(doc) {//背景回调
    initb(doc);
   // render();
};


function init1(data) {//人物初始化
    let groupT = new THREE.Group();
    groupT.name=data.paths.length==11?"tan":"elephant";
    addGeoObject1(groupT,data);
    let box = new THREE.Box3();
    box.setFromObject(groupT);
    for(let j=0;j<groupT.children.length;j++){
        groupT.children[j].position.x -= box.getCenter().x;
        groupT.children[j].position.y -= box.getCenter().y;
    }
    
    character.add(groupT );
}

function initb(data) {//背景初始化
    let groupT = new THREE.Group();
    addGeoObject(groupT,data);
    let box = new THREE.Box3();
    box.setFromObject(groupT);
    for(let j=0;j<groupT.children.length;j++){
        groupT.children[j].position.x -= box.getCenter().x;
        groupT.children[j].position.y -= box.getCenter().y;
    }

    groupB.add(groupT)

    if(groupB.children.length==len){
        //第一个可见
        for(let i=1;i<len;i++){
            groupB.children[i].visible=false;
        }

    //   animate()
       animationID= requestAnimationFrame(animate);
    //    console.log(group)
        render();
    }
}

function addGeoObject1(group, svgObject) {//人物
    let i, j, localLen, len1;
    let simpleShapes, simpleShape;
    let thePaths = svgObject.paths;
    localLen = thePaths.length;
    console.log(`svg`)
    console.log(svgObject)
    for (i = 0; i < localLen; ++i) {

        //必须都是true，否则孔洞或外轮廓会丢失
        simpleShapes = thePaths[i].toShapes(true, true);
    //   simpleShapes = thePaths[i];                  

        len1 = simpleShapes.length;
        for (j = 0; j < len1; ++j) {
            simpleShape = simpleShapes[j];
            simpleShape.autoClose = true;

            //获取点序列的采样方法来自虚基类CurvePath，默认参数12段
            //P.S样条曲线也是这样采样连线显示的（官方文档搜splinecurve）
            let points = simpleShape.createPointsGeometry();
            let cPoints=points.clone();
            let pointsMaterial = new THREE.PointsMaterial({
                color:0xffffff,
                size:0.7,
                transparent:false,//使材质透明
                blending:THREE.AdditiveBlending,
                depthTest:true,//深度测试開啟，不消去场景的不可见面
            //  map:createLightMateria()//刚刚创建的粒子贴图就在这里
            });

             originParticleField = new THREE.Points(points,pointsMaterial);
            pointNum+=points.vertices.length;
            originVer.push(...points.vertices);
            destiVer.push(...cPoints.vertices);
            group.add(originParticleField);
        }   
    }
}

function addGeoObject(group, svgObject) {//背景
    let i, j, local_len, len1;
    let path, mesh, amount, simpleShapes, simpleShape, shape3d, x, toAdd, results = [];
    let thePaths = svgObject.paths;
   // console.log(svgObject)
   local_len = thePaths.length;
 //   console.log(`len: ${len}`)
    for (i = 0; i < local_len; ++i) {

        //必须都是true，否则孔洞或外轮廓会丢失
        simpleShapes = thePaths[i].toShapes(true, true);
     //   simpleShapes = thePaths[i];                  

        len1 = simpleShapes.length;
        for (j = 0; j < len1; ++j) {
            simpleShape = simpleShapes[j];
            simpleShape.autoClose = true;

            //获取点序列的采样方法来自虚基类CurvePath，默认参数12段
            //P.S样条曲线也是这样采样连线显示的（官方文档搜splinecurve）
            let points = simpleShape.createPointsGeometry();
            //let geometry = new THREE.ShapeGeometry(simpleShape);
            let line = new THREE.Line(points, new THREE.LineBasicMaterial({ color: 0xa0a0a0, linewidth: 1 }));
            group.add(line);
        }
    }
};

function animate1() {//人物动画
    particleAnimate();//粒子动画函数
    render();
    if(stop2!=2&&stop4!=1){
        animationID1=requestAnimationFrame(animate1)
   }
   else{
        window.cancelAnimationFrame(animationID1);
        render();
        
   }
   if(stop2==2){
        
       document.removeEventListener("mousedown",onMouseDown,false);
       document.removeEventListener("mouseup",onMouseUp,false);
       document.removeEventListener("touchstart",onMouseDown,false);
       document.removeEventListener("touchend",onMouseUp,false);
       
   }

}


function particleAnimate () {
    stop=0;
  //  stop==1?speedY=speedY*0.1:null;
  
    oDestiVer=stop3==0?oVer:destiVer;
    stop2==1?oDestiVer=destiVer:null;
     for(let i = 0; i < pointNum; i++){
        let originP = originVer[i],
            destiP =  oDestiVer[i];                
        let distance =  Math.abs(originP.y - destiP.y);
   //     alert(distance);
        if(distance<1){
            speedY=2;
        }
        if (distance > 0.5){
            //利用距离与坐标差的余弦值
            originP.y += ((destiP.y - originP.y)/distance) * speedY * (1 - Math.random());
            stop=1;
        }

    }

     originParticleField.geometry.verticesNeedUpdate=true;
    if(stop==0){
       if(stop2==1){
        bus.$emit('message', 'true');
       }
        if(stop3==1){
            //停止动画，visible不变;最终停止
            stop4=1;
        }else{
            //visible变化。
            stop2++;
            if(stop2==1){

                character.children[0].visible=!character.children[0].visible;
                character.children[1].visible=!character.children[1].visible;
                
            }
            oDestiVer=destiVer;
            speedY=4;            
        }
        stop2==0?groupB.visible=true:null;
        stop2==0?animationID=requestAnimationFrame(animate):null;
    }
    
   
    
}

function animate() {//背景动画
    if((Math.PI/2-groupB.rotation.y)<0.05){
      //  alert(currentIndex)
        groupB.rotation.y=-Math.PI/2;
        groupB.children[currentIndex++].visible =false;
        currentIndex=currentIndex%len;
        groupB.children[currentIndex].visible =true;
    }
    groupB.rotation.y += Math.PI/70;
    animationID=requestAnimationFrame( animate );
    render();
};


function groupRotate(){
 //   console.log(character.children[tan].children.length);
    character.children[tan].children[9].position.z+=-10;
    character.children[tan].children[9].quaternion.setFromAxisAngle(new THREE.Vector3(0,1,0),Math.PI/8);
    
    character.children[tan].children[10].position.z+=10;
    character.children[tan].children[10].quaternion.setFromAxisAngle(new THREE.Vector3(0.5,1,0),Math.PI/8);

    for(let i=8;i<=13;i++){
        character.children[ele].children[i].position.z+=-9;
        character.children[ele].children[i].quaternion.setFromAxisAngle(new THREE.Vector3(1,0,0),-Math.PI/8);
    }
}

function render() {
    throttle(renderer.render(scene, camera),100);
}

function throttle(fn,delay){
    return function(){
        if(!timeout){
            timeout=setTimeout(function(){
                fn();
                clearTimeout(timeout);
                timeout=null;
            },delay)
        }
    }
}

function onWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
}


function onMouseDown(e){
    animationID1= requestAnimationFrame(animate1);
   // alert(animationID,animationID1)
   groupB.visible=false;
   render()
   window.cancelAnimationFrame(animationID);
   stop3=0;
   stop4=0;
}

function onMouseUp(e){
    stop3=1;
}